import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { List, InputItem, WhiteSpace, NavBar, Icon, Button, Toast, Flex, Radio } from 'antd-mobile';
import { createForm } from 'rc-form';
import { submitRegister } from '../api/index'
import { getVerigyCode } from '../api/index'
export class Register extends Component {
    constructor(props) {
        super(props)

        this.state = {
            mobile: '',
            code: '',
            email: '',
            pwd: '',
            verifyPwd: '',
            gender: '',
            codeText: ''
        }
    }
    // 
    onChange = gender => {
        this.setState({
            gender
        });
    };
    // 
    handleRegister = () => {
        // validateFieldsjs, error,null
        this.props.form.validateFields((error, value) => {
            if (error) {
                // ,
                Toast.fail('', 2)
            } else {
                var mobile = this.state.mobile.replace(/\s/g, '')
                submitRegister({ ...this.state, mobile }).then(res => {
                    const { meta: { status, msg } } = res.data
                    if (status === 200) {
                        // 
                        Toast.success(msg)
                        // 
                        this.props.history.push('/login')
                    } else {
                        // 
                        Toast.success(msg)
                    }
                })
            }

        })
    }
    // 
    getCode = (e) => {
        // 139 9999 9999 
        var mobile = this.state.mobile.replace(/\s/g, '')
        // 
        if (!/^1[3-9]\d{9}$/.test(mobile)) {
            Toast.fail('', 2)
            return
        } else {
            // 
            let num = 30
            let timeId = setInterval(() => {
                this.setState({
                    codeText: `${num--}`
                })
                if (num === -1) {
                    clearInterval(timeId)
                    this.setState({
                        codeText: ''
                    })
                }
            }, 1000);
            getVerigyCode(mobile).then(res => {
                // 
                const { meta: { status }, message } = res.data
                if (status === 200) {
                    Toast.success(message, 3)
                }
            })
        }

    }

    render() {
        const { getFieldError, getFieldProps } = this.props.form
        const RadioItem = Radio.RadioItem;
        return (
            <div>
                {this.props.location.pathname === '/register' ?
                    <NavBar
                        mode="dark"
                        leftContent={<Icon type='left' />}
                        onLeftClick={() => this.props.history.go(-1)}
                        style={{
                            position: 'fixed',
                            width: '100%',
                            left: 0,
                            top: 0,
                            zIndex: 1,
                        }}
                    >
                        
                </NavBar> : ''
                }

                <List
                    style={{
                        marginTop: 45, position: 'relative'
                    }}
                >
                    <InputItem
                        // 
                        type="phone"
                        placeholder=""
                        // 
                        clear
                        {...getFieldProps('mobile', {
                            // 
                            validateTrigger: 'onBlur',
                            // 
                            rules: [
                                { required: true, message: "" },
                                { min: 11, message: "11" },
                            ]
                        })
                        }
                        // errortrue
                        error={getFieldError('mobile') ? true : false}
                        // 
                        onErrorClick={() => {
                            Toast.info(getFieldError('mobile')[0], 2)
                        }}
                        // stateusername
                        onChange={v => {
                            this.setState({
                                mobile: v
                            })
                        }}
                        // stateusername
                        value={this.state.mobile}
                    >
                        <span className="star">*</span>  
                    </InputItem>
                    {/* codeTextbutton */}
                    <button disabled={this.state.codeText === '' ? false : true} className="get-code" onClick={this.getCode}>{this.state.codeText}</button>
                    <InputItem
                        // 
                        type="number"
                        placeholder=""
                        // 
                        clear
                        {...getFieldProps('code', {
                            // 
                            validateTrigger: 'onBlur',
                            // 
                            rules: [
                                { required: true, message: "" },

                            ]
                        })
                        }
                        // errortrue
                        error={getFieldError('code') ? true : false}
                        // 
                        onErrorClick={() => {
                            Toast.info(getFieldError('code')[0], 2)
                        }}
                        // stateusername
                        onChange={v => {
                            this.setState({
                                code: v
                            })
                        }}
                        // stateusername
                        value={this.state.code}
                    >
                        <span className="star">*</span>
                        
                    </InputItem>
                    <InputItem
                        // 
                        type="email"
                        placeholder=""
                        // 
                        clear
                        {...getFieldProps('email', {
                            // 
                            validateTrigger: 'onBlur',
                            // 
                            rules: [
                                { required: true, message: "" },
                                { type: 'email', message: ' email ' }
                            ]
                        })
                        }
                        // errortrue
                        error={getFieldError('email') ? true : false}
                        // 
                        onErrorClick={() => {
                            Toast.info(getFieldError('email')[0], 2)
                        }}
                        // stateemail
                        onChange={v => {
                            this.setState({
                                email: v
                            })
                        }}
                        // stateemail
                        value={this.state.email}
                    >
                        <span className="star">*</span>  
                    </InputItem>
                    <InputItem
                        type="password"
                        placeholder=""
                        clear
                        {...getFieldProps('pwd', {
                            validateTrigger: 'onBlur',
                            rules: [
                                {
                                    required: true, message: ''
                                },
                                {
                                    min: 6, message: '6'
                                }
                            ]
                        })}
                        error={getFieldError('pwd') ? true : false}
                        onErrorClick={() => {
                            Toast.fail(getFieldError('pwd')[0], 2)
                        }}
                        onChange={v => {
                            this.setState({
                                pwd: v
                            })
                        }}
                        value={this.state.pwd}
                    >
                        <span className="star">*</span>  
                    </InputItem>
                    <InputItem
                        type="password"
                        placeholder=""
                        clear
                        {...getFieldProps('verifyPwd', {
                            validateTrigger: 'onBlur',
                            rules: [
                                {
                                    required: true, message: ''
                                },
                                {
                                    min: 6, message: '6'
                                }
                            ]
                        })}
                        error={getFieldError('verifyPwd') ? true : false}
                        onErrorClick={() => {
                            Toast.fail(getFieldError('verifyPwd')[0], 2)
                        }}
                        onChange={v => {
                            this.setState({
                                verifyPwd: v
                            })
                        }}
                        value={this.state.verifyPwd}
                    >
                        <span className="star">*</span>   
                    </InputItem>
                    <RadioItem key={1} checked={this.state.gender === ''} onChange={() => this.onChange('')}>
                        <span className="star">*</span>  
                    </RadioItem>
                    <RadioItem key={2} checked={this.state.gender === ''} onChange={() => this.onChange('')}>
                        <span className="star">*</span>   
                    </RadioItem>
                    <WhiteSpace />
                    <Flex justify="center">
                        {/*  */}
                        <Button type="primary" size="small" style={{ marginRight: 10 }}
                            className="bottom-button"
                            onClick={this.handleRegister}>
                            
                            </Button>
                        {/*  */}
                        <Button type="warning" size="small" className="bottom-button" style={{ padding: '0 25px' }}
                            onClick={() => this.props.history.push('/login')}>
                            
                            </Button>

                    </Flex>
                    <WhiteSpace />

                </List>

                <style jsx>{`
                    .bottom-button {
                        width: 30%;
                        padding: 10px 0;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                    .star {
                        color: red;
                    }
                    .get-code {
                        display: block;
                        width: 117px;
                        height: 40px;
                        position: absolute;
                        right: 5px;
                       top: -17px;
                        transform: translateY(47%);
                        background-color: #ccc;
                        color: white;
                        font-size: 14px;
                    }
                `}</style>

            </div>
        )
    }
}

export default createForm()(withRouter(Register))
