import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { WingBlank } from 'antd-mobile'
export class ErrorPage extends Component {
    render() {
        return (
            <div>
                <WingBlank>
                    <div className="tips">!  ...</div>
                    <div className="reason">
                        <p></p>
                        <ul className="reason-list">
                            <li><span><i className="gt">&gt;</i></span></li>
                            <li><span><i className="gt">&gt;</i></span></li>
                        </ul>
                    </div>
                    <div className="links">
                        <p>
                        <Link to="/"></Link>
                        <span>|</span>
                        <Link to="/searchfield"></Link>
                        <span>|</span>
                        <Link to="/category"></Link>
                        </p>
                    </div>
                </WingBlank>
                <style jsx>{`
                    .tips {
                        font-size: 25px;
                        margin: 20px auto;
                    }
                    .reason {
                        line-height: 1.6em;
                        margin-bottom: 25px;
                        li {
                            span {
                                color: #ccc;
                                padding-left: 5px;
                            .gt {
                                font-size: 10px;
                                }
                            }
                        }
                    }
                    .links {
                        color: #ccc;
                        line-height: 2em;
                        :global(a) {
                            color: #0B72A4;
                        }
                        span {
                            color: #ccc;
                            padding: 0 3px;
                        }
                    }
                `}</style>
            </div>
        )
    }
}

export default ErrorPage
