let initState = {
    totalNum: 0
}

export const CartReducer = (state = initState, action) => {
    switch (action.type) {
        // 
        case 'SYNC_CART_GOODS':
            var {cart_Infos} = action.payload
            var totalNum = 0;
            // 
            for (var goods_id in cart_Infos) {
                totalNum += cart_Infos[goods_id].amount
            }
            // 
            return {...state, totalNum, ...action.payload}
        case 'ADD_CART':
            state.totalNum += 1
            return {...state}
        // 
        case 'BUY_NOW':
            return {...state, ...action.payload}
        case 'CLEAR': 
            return {}
        default:
        // 
        return state
    }
    
}