import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import ReactDOM from 'react-dom'
import { SearchBar, WingBlank, Flex, ActivityIndicator, PullToRefresh, Toast } from 'antd-mobile'
import { searchGoods } from '../api/index'
import qs from 'querystring'
export class SearchGoods extends Component {
    constructor(props) {
        super(props)

        this.state = {
            goodsList: [],
            bottom: false,
            animating: false,
            refreshing: false,
            down: false,
            height: document.documentElement.clientHeight,
            pagenum: 2,
        }
    }
    UNSAFE_componentWillMount() {
        // 
        this.setState({ animating: true })
        // 
        var searchValue = this.props.match.params.goodsvalue
        
        // // 
        searchGoods(searchValue).then(res => {
            // 
            const { meta: { status }, message: { goods, total } } = res.data
            if (status === 200) {
                // 
                if (total === 0) {
                    this.setState({
                        animating: false,
                        refreshing: false,
                        bottom: true
                    })
                     // 2
                    Toast.info('...', 2, () => this.props.history.push('/'))
                   
                }
                this.setState({
                    goodsList: goods
                })
            }
            // 2020
            if (goods.length < 20) {
                this.setState({
                    bottom: true
                })
            }
        })
    }
    UNSAFE_componentDidMount() {
        const height = this.state.height - ReactDOM.findDOMNode(this.ptr).offsetTop;
        this.setState({
            height
        })
    }
    // 
    getMoreGoods = () => {
        // 
        const query = this.props.match.params.goodsvalue
        // 
        const pagenum = this.state.pagenum
        const searchData = qs.stringify({ query, pagenum })
        searchGoods(searchData).then(res => {
            // 
            const { meta: { status }, message: { goods } } = res.data
            if (status === 200) {
                // 
                if (!goods.length) {
                    this.setState({
                        refreshing: false,
                    })
                }
                // 20
                if (goods.length !== 20) {
                    this.setState({
                        bottom: true
                    })
                    Toast.info('')
                }
                // 
                this.setState({
                    goodsList: this.state.goodsList.concat(goods)
                })
            }
        })
        // +1
        this.setState({
            pagenum: this.state.pagenum + 1
        })
    }
    render() {
        return (
            <div>
                {/*  */}
                <ActivityIndicator
                    toast
                    text="..."
                    animating={this.state.animating}
                />
                {/*  */}
                <div style={{display: 'flex',backgroundColor: '#efeff4',zIndex:999}}>
                    <i className="iconfont icon-arrow-left" 
                    style={{width: 30, alignSelf: 'center',  padding: '0 10px'}}
                    onClick={() => this.props.history.goBack()}
                    ></i>
                    <SearchBar placeholder=""
                    onFocus={() => this.props.history.push('/searchfield')}
                    style={{flex: 1}}
                />
                </div>
                {/*  */}
                <PullToRefresh
                // damping
                    damping={100}
                    ref={el => this.ptr = el}
                    style={{
                        height: this.state.height,
                        overflow: 'auto',
                    }}
                    indicator={this.state.down ? {} : { deactivate: '' }}
                    // direction
                    direction={this.state.down ? 'down' : 'up'}
                    // refreshing	
                    refreshing={this.state.refreshing}
                    // onRefresh	, 
                    onRefresh={() => {
                        // true
                        this.setState({ refreshing: true });
                        // 
                        this.getMoreGoods()
                    }}
                >
                    {/* WingBlank size */}
                    <WingBlank size="middle">
                        <Flex
                            justify="between"
                            wrap="wrap"
                        >
                            {this.state.goodsList.map(v => (
                                v.goods_small_logo ?
                                    <div key={v.goods_id} className="good" onClick={() => this.props.history.push(`/goodsdetail/${v.goods_id}`)}>
                                        <div className="good_content">
                                            <img src={v.goods_small_logo}
                                                // 
                                                onLoad={() => {
                                                    this.setState({
                                                        animating: false,
                                                        refreshing: false
                                                    })

                                                }}
                                                alt="" />
                                            <div className="describe ellipsis-1">{v.goods_name}</div>
                                            <div className="price"><span>&yen;</span>{v.goods_price}</div>
                                        </div>
                                    </div> : ''
                            ))}

                        </Flex>
                    </WingBlank>
                    {/* bottom */}
                    {this.state.bottom ?
                        <div className="goods-list-bottom">
                            <div className="line">
                                <span> </span>
                            </div>
                        </div> : ''
                    }
                </PullToRefresh>

                <style jsx>{`
                    .goods-list-bottom {
                        height: 40px;
                        line-height: 40px;
                        text-align: center;
                        font-size: 14px;
                        color: #ccc;
                        display: flex;
                        position: relative;
                        align-items: center;
                        .line {
                            width: 200px;
                            height: 1px;
                            vertical-align: middle;
                            background-color: #ccc;
                            margin: 0 auto;
                            text-align: center;
                            span {
                                background-color: #f5f5f9;
                                padding: 0 10px;
                                position: absolute;
                                top: -50%;
                                font-size: 10px;
                                transform: translate(-50%, 50%);
                            }
                        }
                    }
                    .ellipsis-1 {
                        overflow: hidden; 
                        text-overflow: ellipsis; 
                        white-space: nowrap; 
                    }

                    .good {
                        width: 49.5%;
                        border-radius: 20px;
                        padding: 10px;
                        margin-top: 6px;
                        background-color: #fff;
                        .good_content {
                            img {
                                margin: 0 auto;
                                width: 70%;
                                display: block;
                            }
                            .describe {
                                padding: 10px 5px;
                                font-size: 13px;
                                color: #333;        
                            }
                            .price {
                                font-size: 14px;
                                color: red;
                                span {
                                    font-size: 10px;
                                }
                            }
                        }
                    }

            `}</style>
            </div>
        )
    }
}

export default withRouter(SearchGoods)
