import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { Carousel, Flex, WingBlank, WhiteSpace, SearchBar, ActivityIndicator } from 'antd-mobile';
import { getHomeCarousel, getHomeGoodslist } from '../api/index'
import qs from 'querystring'
export class Home extends Component {
    constructor(props) {
        super(props)

        this.state = {
            // Carousel>1autoplay
            carouselList: [{ goods_id: 1 }, { goods_id: 2 }],
            imgHeight: '',
            // 
            goodsList: [],
            // 
            bottom: false,
            // 
            placeholderPre: '',
            animating: false
        }
    }
    // render
    UNSAFE_componentWillMount() {
        // 
        this.setState({ animating: true })

        // 
        getHomeCarousel().then(res => {
            // 
            const { message, meta: { status } } = res.data
            // 200
            if (status === 200) {
                // carouselList
                this.setState({
                    carouselList: message
                })
            }
        })
        // 
        getHomeGoodslist().then(res => {
            // 
            const { message, meta: { status } } = res.data
            // 200
            if (status === 200) {
                // goodsList
                this.setState({
                    goodsList: message
                })
                //  16 => 4 * 40~15
                const index = Math.floor((Math.random() * 16))
                // 
                const i = Math.floor(index / 4)
                const j = index % 4
                // 
                this.setState({
                    placeholderPre: message[i].goods[j].goods_name.slice(0, 10) + '...'
                })
            }
        }).then(() => {
            // 
            this.setState({
                bottom: true
            })
        })
    }
    // 
    handleSearchSimilar = (cid) => {
        this.props.history.push('/searchgoods/' + qs.stringify({ cid }))
    }
    render() {
        return (
            // 
            <div>
                {/*  */}
                <ActivityIndicator
                    toast
                    text="..."
                    animating={this.state.animating}
                />
                {/*  */}
                {this.props.location.pathname === '/'? 
                    <SearchBar placeholder={this.state.placeholderPre}
                    onFocus={() => this.props.history.push('/searchfield')}
                    style={{ position: 'fixed', top: 0, left: 0, width: '100%'}}
                />: ''
                }
                {/*  */}
                <Carousel
                    autoplay={true}
                    infinite
                    style={{ marginTop: 44 }}
                >
                    {this.state.carouselList.map(val => (
                        <img key={val.goods_id}
                            src={val.image_src}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top', imgHeight: this.state.imgHeight }}
                        />
                    ))}
                </Carousel>
                {/*  */}
            <div className="catitems">
                    <div onClick={() => this.props.history.push('/searchgoods/query=')}><img src="https://www.zhengzhicheng.cn/pyg/icon_index_nav_3@2x.png" alt="" /></div>

                    <div onClick={() => this.props.history.push('/searchgoods/query=')}><img src="https://www.zhengzhicheng.cn/pyg/icon_index_nav_2@2x.png" alt="" /></div>

                    <div onClick={() => this.props.history.push('/searchgoods/query=')}><img src="https://www.zhengzhicheng.cn/pyg/icon_index_nav_1@2x.png" alt="" /></div>

                    <div onClick={()=>this.props.history.push('/searchgoods/query=')}><img src="https://www.zhengzhicheng.cn/pyg/icon_index_nav_5@2x.png" alt="" /></div>
                </div>
                {/*  */}
                <div className="goodsList">
                    {this.state.goodsList.map(item => (
                        <div key={item.group_img} className="goods">
                            {/* WhiteSpace size */}
                            <WhiteSpace size="sm" />
                            <img src={item.group_img}
                                // 
                                onLoad={() => {
                                    this.setState({ animating: false })
                                }}
                                alt="" />
                            {/* WingBlank size */}
                            <WingBlank size="sm">
                                 {/* flex */}
                                <Flex
                                    justify="between"
                                    wrap="wrap"
                                >
                                    {item.goods.map(v => (
                                        <div key={v.goods_id} className="good">
                                            <div className="good_content"
                                                onClick={() => this.props.history.push(`/goodsdetail/${v.goods_id}`)}
                                            >
                                                <img src={v.goods_small_logo} alt="" />
                                                <div className="describe ellipsis-1">{v.goods_name}</div>
                                                <div className="price">&yen;{v.goods_price}</div>
                                            </div>
                                            <button
                                                className='search-similar'
                                                onClick={() => this.handleSearchSimilar(v.cat_id)}
                                            >
                                                
                                            </button>
                                        </div>
                                    ))}
                                </Flex>
                            </WingBlank>
                        </div>
                    ))
                    }

                </div>
                {/* bottom */}
                {this.state.bottom ? <div className="goods-list-bottom">
                    <div className="line">
                        <span>~</span>
                    </div>

                </div> : ''}
                {/* CSS */}
                <style jsx>{`
                .search-similar {
                    border: 1px solid rgb(16, 142, 233);
                    width: 55%;
                    display: block;
                    margin: 10px auto;
                    background-color: #fff;
                    padding: 5px 10px;
                    border-radius: 5px;
                    color: rgb(16, 142, 233);
                }
                .goods-list-bottom {
                    height: 40px;
                    line-height: 40px;
                    text-align: center;
                    font-size: 14px;
                    color: #ccc;
                    display: flex;
                    position: relative;
                    align-items: center;
                    .line {
                        width: 300px;
                        height: 1px;
                        vertical-align: middle;
                        background-color: #ccc;
                        margin: 0 auto;
                        text-align: center;
                        span {
                            background-color: #f5f5f9;
                            padding: 0 20px;
                            position: absolute;
                            top: -50%;
                            transform: translate(-50%, 50%);
                        }
                    }
                }
                .ellipsis-1 {
                    overflow: hidden; 
                    text-overflow: ellipsis; 
                    white-space: nowrap;
                }

                .catitems {
                    display: flex;
                    background-color: #fff;
                    padding: 10px;

                    div {
                        flex: 1;
                        text-align: center;

                        img {
                            margin: 0 auto;
                            width: 50%;
                        }
                    }
                }
                
                .goodsList {
                    .goods {

                        >img {
                            display: block;
                        }
                        
                        .good {
                            width: 49.5%;
                            border-radius: 20px;
                            padding: 10px;
                            margin-top: 6px;
                            background-color: #fff;

                            .good_content {
                                
                                img {
                                    
                                    width: 60%;
                                    margin: 0 auto;
                                    display: block;
                                }

                                .describe {
                                    font-size: 13px;
                                    color: #333;
                                    padding: 10px 5px;
                                }

                                .price {
                                    font-size: 13px;
                                    color: red;
                                }
                            }

                            .similar {
                                width: 80%;
                                display: block;
                                margin: 5px auto 0;
                            }

                        }
                        
                    }
                }
                `}</style>
            </div>
        )
    }
}

export default withRouter(Home)
