import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import {connect} from 'react-redux'
import { List, InputItem, WhiteSpace, NavBar, Icon, Button, Toast, Flex } from 'antd-mobile';
import { createForm } from 'rc-form';
import {submitLogin, getCartGoods} from '../api/index'
import axios from 'axios'
export class Login extends Component {
    constructor(props) {
        super(props)

        this.state = {
            //  13999999999123456
            // 
            username: '',
            // 
            password: ''
        }
    }
    UNSAFE_componentWillMount() {
        if(this.props.location.state && this.props.location.state.from.pathname !== '/login') {
            // 
        // this.props.location.state
            Toast.info('', 1)
        }
    }
    // 
    handleLogin = () => {
        this.props.form.validateFields((error, value) => {
            // valuegetFieldProps
            if (error) {
                // ,
                Toast.fail('', 2)
            } else {
                let obj = {
                    // 139 9999 9999 
                    username: value.username.replace(/\s/g, ''),
                    password: value.password
                  }
                submitLogin(obj).then(res => {
                    // 
                    const {meta: {status, msg}, message} = res.data
                    // 200
                    if (status === 200) {
                        // token
                        let {token} = message
                        axios.defaults.headers.common['Authorization'] = token
                        // userReducer
                        this.props.changeLoginState({Login: true, token})
                        // 
                        getCartGoods().then(res => {
                            // 
                            const { meta: { status }, message } = res.data
                            // 200
                            if (status === 200) {
                                // 
                                console.log(message.cart_info);
                                if (message.cart_info) {
                                    // CartReducer
                                    this.props.snycCartGoods(Object.values(JSON.parse(message.cart_info)))
                                }

                            }

                        })
                        // locationfrom
                        const {from} = this.props.location.state || {from: {pathname: '/'}}
                        // pathname
                        let pathname = from.pathname
                        if (pathname === '/login') {
                            pathname = '/'
                        }
                        // 2
                        Toast.success(msg, 2, () => {
                            this.props.history.push(pathname)
                        })
                    } else {
                        // 
                        Toast.fail(msg, 2)
                    }
                })
            }
        })

    }

    render() {
        const { getFieldError, getFieldProps } = this.props.form;
        return (
            <div>
                    {this.props.location.pathname === '/login'? <NavBar
                    mode="dark"
                    leftContent={<Icon type='left' />}
                    onLeftClick={() => this.props.history.push('/')}
                    style={{
                        position: 'fixed',
                        width: '100%',
                        left: 0,
                        top: 0,
                        zIndex: 1,
						                        backgroundImage: 'linear-gradient(to left,#FCA5F1,#B5FFFF)',
                        color:'#000000'
                    }}
                >
                    
                </NavBar>: ''}
                <List
                    style={{
                        marginTop: 45
                    }}
                    renderHeader={() => ''}
                >
                    <InputItem
                        // 
                        type="phone"
                        placeholder=""
                        // 
                        clear
                        {...getFieldProps('username', {
                            // 
                            validateTrigger: 'onBlur',
                            // 
                            rules: [
                                { required: true, message: "" },
                                { min: 11, message: "11" },
                            ]
                        })
                        }
                        // errortrue
                        error={getFieldError('username') ? true : false}
                        // 
                        onErrorClick={() => {
                            Toast.info(getFieldError('username')[0], 2)
                        }}
                        // stateusername
                        onChange={v => {
                            this.setState({
                                username: v
                            })
                        }}
                        // stateusername
                        value={this.state.username}
                    >
                        
                    </InputItem>
                    <InputItem
                        type="password"
                        placeholder=""
                        clear
                        {...getFieldProps('password', {
                            validateTrigger: 'onBlur',
                            rules: [
                                {
                                    required: true, message: ''
                                },
                                {
                                    min: 6, message: '6'
                                }
                            ]
                        })}
                        error={getFieldError('password') ? true: false}
                        onErrorClick={() => {
                            Toast.fail(getFieldError('password')[0], 2)
                        }}
                        onChange={v => {
                            this.setState({
                                password: v
                            })
                        }}
                        value={this.state.password}
                    >
                        
                    </InputItem>
                    <WhiteSpace />
                        <Flex justify="center">
                            <Button type="primary"   size="small"
                            className="bottom-button"
                            style={{marginRight: 10}}
                            onClick={this.handleLogin}>
                                
                            </Button>
                            <Button type="warning"  size="small"
                            className="bottom-button"
                            onClick={() => this.props.history.push('/register')}>
                                
                            </Button>
                        </Flex>
                    <WhiteSpace/>
                </List>

                <style jsx>{`
                        .bottom-button {
                            width: 30%;
                            padding: 10px 0;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                        }
                `}</style>
            </div>
        )
    }
}
// // state

const mapStateToProps = state => {
    return {
        loginState: state.userModule.loginState
    }
}

// userReducertrue
const mapActionToProps = dispatch => {
    return {
        changeLoginState: newState => {
            dispatch({type: 'CHANGE_LOGIN_STATE', payload: newState})
        },
        snycCartGoods: cart_Infos => {
            dispatch({type: 'SYNC_CART_GOODS', payload: {cart_Infos}})
        }
    }
}
// mapStateToPropsnull
export default connect(mapStateToProps, mapActionToProps)(createForm()(withRouter(Login)))
