import React, { Component } from 'react'
import { connect } from 'react-redux'
import { List, InputItem , NavBar, Icon, Toast, Flex, Button, WhiteSpace, Picker, TextareaItem} from 'antd-mobile';
import {cityData} from '../data/citys'
import { createForm } from 'rc-form';
import {withRouter} from 'react-router-dom'

export class AddressInfo extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             name: '',
             phone: '',
             address: '',
             emailCode: ''
        }
    }
    SaveAddressInfo = () => {
        // validateFieldsjs, error,null
        this.props.form.validateFields((error, value) => {
            if (error) {
                // ,
                Toast.fail('', 2)
            } else {
                // 
                let address = document.querySelector('.am-list-extra').innerText.split(',').join('') + this.state.address
                this.props.SaveAddressInfo(this.state.name, this.state.phone, address)
                Toast.success('', 2, () => {
                    this.props.history.goBack()
                })
            }
    })
}
    render() {
        const { getFieldProps, getFieldError } = this.props.form;
        return (
            <div>
                <NavBar
                    mode="dark"
                    leftContent={<Icon type='left' />}
                    onLeftClick={() => this.props.history.goBack()}
                    style={{
                        position: 'fixed',
                        width: '100%',
                        left: 0,
                        top: 0,
                        zIndex: 1,
                    }}
                >
                    
                </NavBar>
                <List
                    style={{
                        marginTop: 45
                    }}
                >
                    <InputItem
                        placeholder=""
                        //  
                        clear
                        {...getFieldProps('name', {
                            // 
                            validateTrigger: 'onBlur',
                            // 
                            rules: [
                                { required: true, message: "" },
                                { min: 2, message: "2" },
                            ]
                        })
                        }
                        // errortrue
                        error={getFieldError('name') ? true : false}
                        // 
                        onErrorClick={() => {
                            Toast.info(getFieldError('name')[0], 2)
                        }}
                        // statename
                        onChange={v => {
                            this.setState({
                                name: v
                            })
                        }}
                        // statename
                        value={this.state.name}
                    >
                       <span style={{color: 'red'}}>*</span> 
                    </InputItem>
                    <InputItem
                        // 
                        type="phone"
                        placeholder=""
                        // 
                        clear
                        {...getFieldProps('phone', {
                            // 
                            validateTrigger: 'onBlur',
                            // 
                            rules: [
                                { required: true, message: "" },
                                { min: 11, message: "11" },
                            ]
                        })
                        }
                        // errortrue
                        error={getFieldError('phone') ? true : false}
                        // 
                        onErrorClick={() => {
                            Toast.info(getFieldError('phone')[0], 2)
                        }}
                        // statephone
                        onChange={v => {
                            this.setState({
                                phone: v
                            })
                        }}
                        // statephone
                        value={this.state.phone}
                    >
                       <span style={{color: 'red'}}>*</span> 
                    </InputItem>
                    <Picker
                        data={cityData}
                        title="Areas"
                        {...getFieldProps('district', {
                            initialValue: ["440000", "440100", "440106"],
                        })}
                        >
                        <List.Item arrow="horizontal"><span style={{color: 'red'}}>*</span></List.Item>
                    </Picker>
                    
                    <InputItem
                        placeholder=""
                        // 
                        clear
                        {...getFieldProps('address', {
                            // 
                            validateTrigger: 'onBlur',
                            // 
                            rules: [
                                { required: true, message: "" },
                                { min: 5, message: "5" },
                            ]
                        })
                        }
                        // errortrue
                        error={getFieldError('address') ? true : false}
                        // 
                        onErrorClick={() => {
                            Toast.info(getFieldError('address')[0], 2)
                        }}
                        // stateaddress
                        onChange={v => {
                            this.setState({
                                address: v
                            })
                        }}
                        // stateaddress
                        value={this.state.address}
                    >
                       <span style={{color: 'red'}}>*</span> 
                    </InputItem>
                    <InputItem
                        placeholder=""
                        // 
                        clear
                        // stateemailCode
                        onChange={v => {
                            this.setState({
                                emailCode: v
                            })
                        }}
                        // stateemailCode
                        value={this.state.emailCode}
                    >
                       
                    </InputItem>
                    <TextareaItem
                        placeholder=""
                        rows={5}
                        count={100}
                    />
                    <WhiteSpace />
                        <Flex justify="center">
                            <Button type="primary"   size="small" 
                                className="bottom-button"
                                style={{marginRight: 10}}
                                onClick={this.SaveAddressInfo}
                            >
                                
                            </Button>
                            <Button type="warning"  size="small" 
                                className="bottom-button"
                                onClick={this.props.history.goBack}
                            >
                                
                            </Button>      
                        </Flex>
                    <WhiteSpace/>     
                </List>
            </div>
        )
    }
}



const mapDispatchToProps = dispatch => {
    return {
        SaveAddressInfo: (name, phone, address) => {
            dispatch({type: 'SAVE_ADDRESS_INFO', payload: {name, phone, address}})
        }
    }    
}

export default connect(null, mapDispatchToProps)(createForm()(withRouter(AddressInfo)))
