import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { Modal, Icon, Checkbox, WingBlank, Stepper, SwipeAction, Toast } from 'antd-mobile'
import emptyCart from '../assets/imgs/cart_empty.png'
import { getCartGoods, syncCart } from '../api/index'
const CheckboxItem = Checkbox.CheckboxItem;
const alert = Modal.alert;
export class Cart extends Component {
    constructor(props) {
        super(props)

        this.state = {
            //  {goods_id: }
            cart_infos: {},
            // 
            cart_infos_Status: true,
            // stepper
            num: '',
            // totalPrice
            totalPrice: 0,
            // allStatus
            allStatus: false,
            // 
            allSelectedNum: 0,
            //  *amount
            selectedGoodsTotalNum: 0,
            // totalNum
            totalNum: 0,
            // manage
            manage: true,
        }
    }
    UNSAFE_componentWillMount() {
        // render
        this.init()
    }

    // 
    init = () => {
        getCartGoods().then(res => {
            // 
            const { meta: { status }, message: { cart_info } } = res.data
            // 200
            if (status === 200) {
                // 
                if (cart_info) {
                    // statecart_infos
                    let cart_infos = JSON.parse(cart_info)
                    // 
                    for (var goods_id in cart_infos) {
                        cart_infos[goods_id].selectedStatus = false
                    }
                    this.setState({
                        cart_infos,
                        cart_infos_Status: true,
                        totalNum: Object.values(JSON.parse(cart_info)).length
                    })
                } else {
                    // false
                    this.setState({
                        cart_infos_Status: false
                    })
                }
            }
        })
    }
    // 
    syncCartGoodsData = () => {
        syncCart({ infos: JSON.stringify(this.state.cart_infos) })
        // CartReducertotalNum
        this.props.snycCartGoods(this.state.cart_infos, this.state.totalPrice, this.state.selectedGoodsTotalNum)
    }

    // stepper)
    handleUpdateNum = (num, goods_id) => {
        // 
        const cart_infos = this.state.cart_infos
        cart_infos[goods_id].amount = num
        this.setState({
            num,
            cart_infos,
        }, () => {
            // 
            this.calTotalPrice()
            // 
            this.syncCartGoodsData()
        })
    }
    // 
    changeSingleSelectedStatus = (e, goods_id) => {
        // 
        let cart_infos = this.state.cart_infos
        cart_infos[goods_id].selectedStatus = e.target.checked
        this.setState({
            cart_infos: cart_infos
        })
        // 
        this.isAllSelected()
        // 
        this.calTotalPrice()
        this.setState({
            allSelectedNum: e.target.checked ? this.state.allSelectedNum + 1 : this.state.allSelectedNum - 1
        })
    }
    // 
    isAllSelected = () => {
        // true
        let allSelected = true
        // 
        for (var goods_id in this.state.cart_infos) {
            if (!this.state.cart_infos[goods_id].selectedStatus) {
                // false
                allSelected = false
                break
            }
        }
        this.setState({
            allStatus: allSelected
        })
    }
    // 
    handleAllChecked = () => {
        // 
        let cart_infos = this.state.cart_infos
        // ,allStatus
        for (var goods_id in cart_infos) {
            cart_infos[goods_id].selectedStatus = this.state.allStatus
        }
        this.setState({
            cart_infos,
            allSelectedNum: this.state.allStatus ? Object.values(cart_infos).length : 0
        })
        // 
        this.calTotalPrice()
    }
    // 
    calTotalPrice = () => {
        let totalPrice = 0
        let selectedGoodsTotalNum = 0
        for (var goods_id in this.state.cart_infos) {
            if (this.state.cart_infos[goods_id].selectedStatus) {
                totalPrice += this.state.cart_infos[goods_id].amount * this.state.cart_infos[goods_id].goods_price
                selectedGoodsTotalNum += this.state.cart_infos[goods_id].amount
            }
        }
        this.setState({
            totalPrice,
            selectedGoodsTotalNum
        })
    }
    // 
    handleDeleteSingleGoods = (goods_id) => {
        let cart_infos = this.state.cart_infos
        // id
        delete cart_infos[goods_id]
        // false
        if (!Object.values(cart_infos).length) {
            this.setState({
                cart_infos_Status: false
            })
        }
        // statecart_infos
        this.setState({
            cart_infos,
            allSelectedNum: this.state.allSelectedNum ? this.state.allSelectedNum - 1 : 0
        }, () => {
            // 
            this.syncCartGoodsData()
            // 
            this.calTotalPrice()
        })

    }
    // 
    handleDeleteBatchGoods = () => {
        // 
        let cart_infos = this.state.cart_infos
        // 
        for (var goods_id in cart_infos) {
            // selectedStatus
            if (cart_infos[goods_id].selectedStatus) {
                delete cart_infos[goods_id]
            }
        }
        if (!cart_infos.length) {
            this.setState({
                cart_infos_Status: false
            })
        }
        // 
        this.setState({
            cart_infos,
            totalPrice: 0,
            allSelectedNum: 0,
            selectedGoodsTotalNum: 0,
            totalNum: Object.values(cart_infos).length
        }, () => {
            this.syncCartGoodsData()
        })
    }
    gotoPay = () => {
        // 
        if (!this.state.allSelectedNum) {
            Toast.fail('', 2)
            return
        }
        // CartReducer
        this.props.snycCartGoods(this.state.cart_infos, this.state.totalPrice, this.state.selectedGoodsTotalNum)
        this.props.history.push('/pay')
    }

    render() {
        return (
            <div>
                {/*  */}
                <nav className="nav-header">
                    <div className="nav-header-left" onClick={() => this.props.history.goBack()}>
                        <Icon type='left' />
                    </div>
                    <div className="nav-header-center">
                        {this.state.totalNum ? `(${this.state.totalNum})` : ''}
                    </div>
                    <div className="nav-header-right">
                        <span onClick={() => this.setState({ manage: this.state.manage ? false : true })} className="manage">
                            {this.state.manage ? '' : ''}
                        </span>
                    </div>
                </nav>
                {this.state.cart_infos_Status ?
                    <WingBlank style={{ marginBottom: 60 }}>
                        <div className="order-list" style={{ marginTop: 55 }}>
                            {Object.values(this.state.cart_infos).map(v => (
                                <SwipeAction
                                    key={v.goods_id}
                                    style={{ marginBottom: 5 }}
                                    autoClose
                                    right={[
                                        {
                                            text: '',
                                            style: { backgroundColor: '#ddd', color: 'white' },
                                        },
                                        {
                                            text: '',
                                            style: { backgroundColor: '#F4333C', color: 'white' },
                                            onPress: () => alert('', '?', [
                                                {
                                                    text: '',
                                                    style: {
                                                        backgroundColor: '#777',
                                                        color: '#fff',
                                                        fontWeight: 700
                                                    }
                                                },
                                                {
                                                    text: '',
                                                    style: {
                                                        backgroundColor: 'rgb(244, 51, 60)',
                                                        color: '#fff',
                                                        fontWeight: 700
                                                    },
                                                    onPress: () => this.handleDeleteSingleGoods(v.goods_id)
                                                },
                                            ]),
                                        },
                                    ]}
                                >
                                    <CheckboxItem
                                        checked={v.selectedStatus}
                                        onChange={e => this.changeSingleSelectedStatus(e, v.goods_id)}
                                    >
                                        <div className="single-order">
                                            <img src={v.goods_small_logo}
                                                onClick={() => this.props.history.push(`/goodsdetail/${v.goods_id}`)}
                                                alt="" />
                                            <div className="order-content">
                                                <div className="order-title ellipsis-2"
                                                    onClick={() => this.props.history.push(`/goodsdetail/${v.goods_id}`)}
                                                >
                                                    {v.goods_name}
                                                </div>
                                                <Stepper
                                                    style={{ width: '100%', maxWidth: 100, position: 'absolute', right: 5, bottom: -5, fontSize: 8 }}
                                                    showNumber
                                                    max={v.goods_number}
                                                    min={1}
                                                    defaultValue={v.amount}
                                                    onChange={num => this.handleUpdateNum(num, v.goods_id)}
                                                />
                                                <div className="order-price">
                                                    <span>&yen;</span>
                                                    <span>{v.goods_price}.00</span>
                                                </div>
                                            </div>
                                        </div>
                                    </CheckboxItem>
                                </SwipeAction>
                            ))}
                        </div>
                    </WingBlank>
                    : <div className="empty-cart">
                        {/* import */}
                        <img src={emptyCart} alt="" className="empty-cart-img" />
                        <div className="empty-cart-text1"></div>
                        <div className="empty-cart-text2">~</div>
                        <div className="btn" onClick={() => this.props.history.push('/')}></div>
                    </div>
                }


                <div className="cart-footer">
                    <div className="cart-footer-left" >
                        <CheckboxItem
                            checked={this.state.allStatus}
                            onChange={() => {
                                this.setState({
                                    allStatus: !this.state.allStatus
                                },
                                    // handleAllChecked
                                    () => this.handleAllChecked())
                            }}
                        >
                            
                        </CheckboxItem>
                    </div>
                    {this.state.manage ?
                        <div className="cart-footer-center">
                            <span></span>
                            <span className="total-price"> {this.state.totalPrice}</span>
                        </div> : ''
                    }
                    {this.state.manage ?
                        <div className="cart-footer-right" onClick={this.gotoPay}>
                            <span className="goto-pay">{this.state.allSelectedNum ? `(${this.state.allSelectedNum}` : ''}</span>
                        </div>
                        :
                        <button className="delete-batch"
                            onClick={() => this.state.selectedGoodsTotalNum? alert(`${this.state.allSelectedNum}`, '?', [
                                {
                                    text: '', style: {
                                        backgroundColor: '#777',
                                        color: '#fff',
                                        fontWeight: 700
                                    }
                                },
                                {
                                    text: '', style: {
                                        backgroundColor: 'rgb(244, 51, 60)',
                                        color: '#fff',
                                        fontWeight: 700
                                    }, onPress: () => this.handleDeleteBatchGoods()
                                },
                            ]):Toast.fail('',2)}

                        ></button>
                    }

                </div>

                <style jsx>{`
                    .nav-header {
                        position: fixed;
                        top:0;
                        z-index: 999;
                        display: flex;
                        width: 100%;
                        height: 45px;
                        justify-content: space-between;
                        padding: 0 10px;
                        align-items: center;
                        background-color: #108ee9;
                        color: #fff;
                        font-size: 16px;
                        
                        .nav-header-left {
                            margin-left: 5px;
                            display: flex;
                            align-items: center;
                        }
                        .nav-header-center {
                            position: absolute;
                            left: 50%;
                            top: 50%;
                            transform: translate(-50%, -50%);
                        }
                        .nav-header-right {
                            background-color: transparent;
                        }
                    }
                    .ellipsis-2 {
                        display: -webkit-box;
                        overflow: hidden;
                        white-space: normal!important;
                        text-overflow: ellipsis;
                        word-wrap: break-word;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                    }
                    .empty-cart {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        margin-top: 100px;
                        height: 100%;
                        .empty-cart-img {
                            height: 90px;
                            width: 90px;
                        }
                        .empty-cart-text1 {
                            font-size: 18px;
                            color: #666;
                            padding: 15px 0;
                        }
                        .empty-cart-text2 {
                            color: #888;
                            margin-bottom: 10px;
                        }
                        .btn {
                            font-size: 20px;
                            padding: 15px 55px;
                            text-align: center;
                            margin: 0 auto;
                            border-radius: 10px;
                            background: #ed601b;
                            color: #fff;
                        }
                    }
                    .order-list { 
                        .single-order {
                            background-color: #fff;
                            padding: 5px;
                            display: flex;
                            align-items: center;
                            border-radius: 10px;
                            position: relative;
                            margin-top: 5px;

                            img {
                                width: 40%;
                                height: 40%;
                                flex: 1;
                                padding: 10px;
                            }

                            .order-content {
                                flex: 4;
                                .order-title {
                                    position: absolute;
                                    top: 15px;
                                    padding-right: 5px;
                                    font-size: 12px;
                                }

                                .order-price {
                                    position: absolute;
                                    bottom: 5px;
                                    color: red;

                                    span {
                                        font-size: 12px;
                                    }
                                }
                            }

                        }
                    } 
                    
                    .cart-footer {
                        position: fixed;
                        bottom: 50px;
                        display: flex;
                        justify-content: space-between;
                        height: 50px;
                        line-height: 50px;
                        width: 100%;
                        border-top: 1px solid #e7e7e7;
                        background-color: #fff;
                        .cart-footer-left {
                            display: flex;
                            justify-content: cneter;
                            align-items: center;
                            span {
                            display: block;
                            height: 50px;
                            padding: 0 5px;
                            }
                        }
                        .cart-footer-center {
                            .total-price {
                                color: #ff5500;
                            }
                        }
                        .cart-footer-right {
                            display: flex;
                            width: 100px;
                            flex-direction: column;
                            text-align: center;
                            background-color: #ff5500;
                            .goto-pay {
                            color: #fff;
                            }
                        }
                    }
                    
                    .delete-batch {
                        width: 70px;
                        height: 70%;
                        align-self: center;
                        color: #e94f4f;
                        border-radius: 20px;
                        display: block;
                        border: 1px solid #e94f4f;
                        background-color: transparent;
                        margin-right: 10px;
                    }
                    
                `}</style>
            </div >
        )
    }
}
// 
const mapActionToProps = (dispatch) => {
    return {
        // 
        snycCartGoods: (cart_Infos, totalPrice, selectedGoodsTotalNum) => {
            dispatch({ type: 'SYNC_CART_GOODS', payload: { cart_Infos, totalPrice, selectedGoodsTotalNum } })
        }
    }
}

export default connect(null, mapActionToProps)(withRouter(Cart))
